import { useSelector, useDispatch } from 'react-redux';
import { posts, authors } from './Redux/selectors/selector';
import { addPostHandler } from './Redux/actions/home.actions';
import PostComponent from './PostComponent'
import React, { useState, useEffect } from 'react';

import './App.css';


function App() {
  const postStore = useSelector(posts);
  const authorsStore = useSelector(authors);
  const dispatch = useDispatch();
  const [content, setContent] = useState('');
  const [image, setimage] = useState('');
  const [id, setId] = useState(0);
  const [isDisabled, setIsDisabled] = useState(true);
  const authorData = authorsStore[id];
  const reset = () => {
    setContent('');
    setimage('');
  }
  useEffect(() => {
    if(content !== '' && image !== '') {
      setIsDisabled(false);
    } else {
      setIsDisabled(true);
    }
  }, [content, image, setIsDisabled]);

  const handleFromActionSubmit = (event) => {
    event.preventDefault();
    reset();
    dispatch(addPostHandler({ ...authorData, content, image, comments: 0, shares: 0, likes: 0 }))
  }

  const handleContentSetings = (event) => {
    setContent(event.target.value)
  }

  const handleImgSettings = (event) => {
    setimage(event.target.value)
  }

  const handleSelectPosts = (event) => {
    setId(event.target.value)
  }

  return (
    <div className="App">
      <div className="newPost">
        <form className="form" onSubmit={handleFromActionSubmit}>
          <input placeholder="Type here content of your post" className="input" onChange={handleContentSetings} value={content}/>
          <input placeholder="Type here URL of image" className="input" onChange={handleImgSettings} 
            title='to show your image, paste a valid URL' value={image}/>
          <select id="selectId"
            aria-label="Choose your post"
            onChange={handleSelectPosts}
          >
            { authorsStore.map((author, index) => {
              return(
                <option
                  value={author.id}
                  key={index}
                >
                {author.name}
                </option>
              )
            }) }
          </select>
          <button className="btn" type="submit" disabled={isDisabled}>Create post</button>
        </form>
      </div>
        { postStore.map((post, index) => {
            return(
              <PostComponent key={index} 
                name={post.name}
                photo={post.photo}
                nickname={post.nickname}
                content={post.content}
                image={post.image}
                comments={post.comments}
                shares={post.shares}
                likes={post.likes}
                id={index}
              />
            )
          })
        }
    </div>
  );
}

export default App;
