import React from 'react'
import './App.css'

export default function PostComponent({ name, nickname, content, image }) {
    console.log(image)
    return (
        <React.Fragment>
            <div className="post-author">
                <h1 
                    className="post-author__name"
                > { name }
                </h1>
                <p 
                    className="post-author__nickname"
                >  { nickname }
                </p>
            </div>
            <div className="post-content">
                <p
                    className="post-content__post"
                >
                    { content }
                </p>
                <img 
                    className="post-content__img"
                    src={ image }
                    alt="Name"
                />
            </div>
        </React.Fragment>
    )
}
